import React, { useState, useEffect } from 'react';
import {
  Container,
  Paper,
  Typography,
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControlLabel,
  Switch,
  Alert,
  Chip,
  MenuItem,
  Select,
  FormControl,
  InputLabel
} from '@mui/material';
import {
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  ArrowUpward as UpIcon,
  ArrowDownward as DownIcon,
  Save as SaveIcon,
  Cancel as CancelIcon
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';

export default function AdminPrompts() {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  const [prompts, setPrompts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [openDialog, setOpenDialog] = useState(false);
  const [editingPrompt, setEditingPrompt] = useState(null);
  const [, forceUpdate] = useState(0); // forceUpdate를 위한 state
  const [formData, setFormData] = useState({
    name: '',
    category: 'summary',
    prompt: '',
    description: '',
    isActive: true,
    order: 1
  });

  // prompts 상태 변경 추적
  useEffect(() => {
    console.log('Prompts state changed:', prompts, 'length:', prompts.length);
  }, [prompts]);

  // 카테고리 옵션
  const categories = [
    { value: 'script', label: '스크립트' },
    { value: 'transcript', label: '타임스탬프' },
    { value: 'summary', label: '요약' },
    { value: 'analysis', label: '분석' },
    { value: 'action_points', label: '핵심 포인트' },
    { value: 'scene_analysis', label: '장면 분석' },
    { value: 'blog_seo', label: '블로그 SEO' },
    { value: 'custom', label: '사용자 정의' }
  ];

  useEffect(() => {
    // 관리자 권한 체크
    if (!user || user.role !== 'admin') {
      navigate('/');
      return;
    }
    console.log('useEffect: Loading prompts...');
    loadPrompts();
  }, [user, navigate]);
  
  // 컴포넌트가 마운트될 때 loadPrompts 다시 호출
  useEffect(() => {
    if (user && user.role === 'admin') {
      console.log('Reloading prompts after mount...');
      loadPrompts();
    }
  }, []);

  const loadPrompts = async () => {
    console.log('loadPrompts called');
    setLoading(true);
    setError('');
    try {
      console.log('Loading prompts from /api/system-prompts...');
      const response = await api.get('/system-prompts');
      console.log('Response:', response.data);
      
      // response.data가 이미 data 배열인지 확인
      let promptsData = [];
      if (Array.isArray(response.data)) {
        // response.data가 직접 배열인 경우
        promptsData = response.data;
      } else if (response.data && response.data.data) {
        // response.data.data가 배열인 경우
        promptsData = response.data.data;
      } else if (response.data && response.data.prompts) {
        // response.data.prompts가 배열인 경우
        promptsData = response.data.prompts;
      }
      
      console.log('Setting prompts state with:', promptsData);
      console.log('Prompts data length:', promptsData.length);
      
      // 강제로 새 배열 생성
      const newPrompts = [...promptsData];
      setPrompts(newPrompts);
      console.log('Called setPrompts with:', newPrompts);
      
      // 강제 리렌더링
      forceUpdate(prev => prev + 1);
      
    } catch (error) {
      console.error('Failed to load prompts:', error);
      setError('프롬프트를 불러오는데 실패했습니다.');
      setPrompts([]);
    } finally {
      setLoading(false);
    }
  };

  const handleOpenDialog = (prompt = null) => {
    if (prompt) {
      setEditingPrompt(prompt);
      setFormData({
        name: prompt.name,
        category: prompt.category,
        prompt: prompt.promptText,
        description: prompt.metadata?.description || prompt.description || '',
        isActive: prompt.isActive,
        order: prompt.orderIndex
      });
    } else {
      setEditingPrompt(null);
      setFormData({
        name: '',
        category: 'summary',
        prompt: '',
        description: '',
        isActive: true,
        order: prompts.length + 1
      });
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditingPrompt(null);
    setFormData({
      name: '',
      category: 'summary',
      prompt: '',
      description: '',
      isActive: true,
      order: 1
    });
  };

  const handleSave = async () => {
    setError('');
    setSuccess('');
    
    if (!formData.name || !formData.prompt) {
      setError('이름과 프롬프트는 필수입니다.');
      return;
    }

    try {
      if (editingPrompt) {
        // 수정
        const response = await api.put(`/system-prompts/${editingPrompt.id}`, formData);
        if (response.data.success) {
          setSuccess('프롬프트가 수정되었습니다.');
          handleCloseDialog();
          loadPrompts();
        }
      } else {
        // 생성
        const response = await api.post('/system-prompts', formData);
        if (response.data.success) {
          setSuccess('프롬프트가 생성되었습니다.');
          handleCloseDialog();
          loadPrompts();
        }
      }
    } catch (error) {
      console.error('Failed to save prompt:', error);
      setError(error.response?.data?.error || '저장에 실패했습니다.');
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('정말로 삭제하시겠습니까?')) {
      return;
    }

    setError('');
    setSuccess('');
    
    try {
      const response = await api.delete(`/system-prompts/${id}`);
      if (response.data.success) {
        setSuccess('프롬프트가 삭제되었습니다.');
        loadPrompts();
      }
    } catch (error) {
      console.error('Failed to delete prompt:', error);
      setError('삭제에 실패했습니다.');
    }
  };

  const handleToggleActive = async (prompt) => {
    try {
      const response = await api.put(`/system-prompts/${prompt.id}`, {
        ...prompt,
        isActive: !prompt.isActive
      });
      if (response.data.success) {
        loadPrompts();
      }
    } catch (error) {
      console.error('Failed to toggle active:', error);
      setError('상태 변경에 실패했습니다.');
    }
  };

  const handleReorder = async (prompt, direction) => {
    const currentIndex = prompts.findIndex(p => p.id === prompt.id);
    const newIndex = direction === 'up' ? currentIndex - 1 : currentIndex + 1;
    
    if (newIndex < 0 || newIndex >= prompts.length) {
      return;
    }

    const newPrompts = [...prompts];
    const temp = newPrompts[currentIndex];
    newPrompts[currentIndex] = newPrompts[newIndex];
    newPrompts[newIndex] = temp;

    // 순서 업데이트
    try {
      await Promise.all(newPrompts.map((p, index) => 
        api.put(`/system-prompts/${p.id}`, { ...p, order: index + 1 })
      ));
      loadPrompts();
    } catch (error) {
      console.error('Failed to reorder:', error);
      setError('순서 변경에 실패했습니다.');
    }
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Paper sx={{ p: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Typography variant="h4" component="h1">
            시스템 프롬프트 관리
          </Typography>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={() => handleOpenDialog()}
          >
            새 프롬프트 추가
          </Button>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError('')}>
            {error}
          </Alert>
        )}

        {success && (
          <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess('')}>
            {success}
          </Alert>
        )}

        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>순서</TableCell>
                <TableCell>이름</TableCell>
                <TableCell>카테고리</TableCell>
                <TableCell>프롬프트</TableCell>
                <TableCell>상태</TableCell>
                <TableCell align="center">작업</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {console.log('Rendering table body, prompts:', prompts, 'length:', prompts.length)}
              {prompts.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} align="center">
                    프롬프트가 없습니다.
                  </TableCell>
                </TableRow>
              ) : (
                prompts.map((prompt, index) => (
                  <TableRow key={prompt.id}>
                    <TableCell>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        {index + 1}
                        <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                          <IconButton
                            size="small"
                            disabled={index === 0}
                            onClick={() => handleReorder(prompt, 'up')}
                          >
                            <UpIcon fontSize="small" />
                          </IconButton>
                          <IconButton
                            size="small"
                            disabled={index === prompts.length - 1}
                            onClick={() => handleReorder(prompt, 'down')}
                          >
                            <DownIcon fontSize="small" />
                          </IconButton>
                        </Box>
                      </Box>
                    </TableCell>
                    <TableCell>
                      <Typography variant="subtitle2">{prompt.name}</Typography>
                      {(prompt.metadata?.description || prompt.description) && (
                        <Typography variant="caption" color="text.secondary">
                          {prompt.metadata?.description || prompt.description}
                        </Typography>
                      )}
                    </TableCell>
                    <TableCell>
                      <Chip 
                        label={categories.find(c => c.value === prompt.category)?.label || prompt.category} 
                        size="small" 
                        variant="outlined"
                      />
                    </TableCell>
                    <TableCell>
                      <Typography 
                        variant="body2" 
                        sx={{ 
                          maxWidth: 300, 
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                          whiteSpace: 'nowrap'
                        }}
                        title={prompt.promptText}
                      >
                        {prompt.promptText}
                      </Typography>
                    </TableCell>
                    <TableCell>
                      <Switch
                        checked={prompt.isActive}
                        onChange={() => handleToggleActive(prompt)}
                        size="small"
                      />
                      <Typography variant="caption">
                        {prompt.isActive ? '활성' : '비활성'}
                      </Typography>
                    </TableCell>
                    <TableCell align="center">
                      <IconButton
                        size="small"
                        onClick={() => handleOpenDialog(prompt)}
                      >
                        <EditIcon />
                      </IconButton>
                      <IconButton
                        size="small"
                        color="error"
                        onClick={() => handleDelete(prompt.id)}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>

        {/* 프롬프트 편집 다이얼로그 */}
        <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
          <DialogTitle>
            {editingPrompt ? '프롬프트 수정' : '새 프롬프트 추가'}
          </DialogTitle>
          <DialogContent>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 2 }}>
              <TextField
                label="이름"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                fullWidth
                required
              />
              
              <FormControl fullWidth>
                <InputLabel>카테고리</InputLabel>
                <Select
                  value={formData.category}
                  onChange={(e) => setFormData({ ...formData, category: e.target.value })}
                  label="카테고리"
                >
                  {categories.map(cat => (
                    <MenuItem key={cat.value} value={cat.value}>
                      {cat.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <TextField
                label="설명 (선택사항)"
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                fullWidth
                multiline
                rows={2}
              />

              <TextField
                label="프롬프트"
                value={formData.prompt}
                onChange={(e) => setFormData({ ...formData, prompt: e.target.value })}
                fullWidth
                required
                multiline
                rows={6}
                helperText="AI에게 전달될 시스템 프롬프트를 입력하세요."
              />

              <FormControlLabel
                control={
                  <Switch
                    checked={formData.isActive}
                    onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                  />
                }
                label="활성화"
              />
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog} startIcon={<CancelIcon />}>
              취소
            </Button>
            <Button onClick={handleSave} variant="contained" startIcon={<SaveIcon />}>
              저장
            </Button>
          </DialogActions>
        </Dialog>
      </Paper>
    </Container>
  );
}